import React, { Component } from 'react';

export default class Methods extends Component {
  render() {
    return (
      <table className="options">
        <thead>
          <tr>
            <th>Method</th>
            <th>Usage</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>openPopupbox()</td>
            <td>this.refs.popupbox.openPopupbox()</td>
            <td>This method allows you to open Popupbox through reference to Popupbox component.</td>
          </tr>
          <tr>
            <td>closePopupbox()</td>
            <td>this.refs.popupbox.closePopupbox()</td>
            <td>This method allows you to close Popupbox through reference to Popupbox component.</td>
          </tr>
        </tbody>
      </table>
    )
  }
}



